/* stylelint-disable selector-class-pattern */
@import '~assets/styles/h3Imports';

.h3-select-override {
  .h3-select__indicator-separator,
  .error__indicator-separator,
  .disabled__indicator-separator {
    display: none;
  }

  .disabled__indicator {
    svg {
      filter: grayscale(1);
    }
  }

  .h3-select__control,
  .error__control,
  .disabled__control {
    background: transparent;
    background-color: transparent;
    border: none;
    border-radius: var(--border-radius, 8px);
    cursor: pointer;
    font-size: rem(16);
    font-weight: 600;
    height: 100%;
    padding-left: 5px;
    padding-right: 10px;
    width: 100%;
  }

  .h3-select__control--menu-is-open,
  .error__control--menu-is-open {
    svg {
      transform: rotate(180deg);
    }
  }

  .h3-select__control--is-focused {
    border-color: var(--input-focus);
    box-shadow: 0 0 0 2px var(--input-focus);
    height: 100%;
    width: 100%;

    svg {
      path {
        fill: var(--dropdown-icon);
      }
    }
  }

  .error__control--is-focused {
    border-color: var(--input-error);
    box-shadow: 0 0 0 1.5px var(--input-error);
  }

  .disabled__single-value {
    color: var(--input-text-disabled);
  }

  .h3-select__menu {
    border-radius: 8px;
    box-shadow: 0 5px 14px rgba(10, 20, 20, 0.1);
  }

  .h3-select__option,
  .error__option {
    color: var(--text-color);
    font-size: rem(16);
    padding: 13.5px 12px;

    &:not(:first-child) {
      border-top: 1px solid var(--input-disabled);
    }
  }

  .h3-select__option--is-selected,
  .error__option--is-selected {
    background-color: var(--interface-info-background);
    font-weight: 600;

    html[data-theme='default'] & {
      &::after {
        color: var(--input-error);
        content: url('../../assets/icons/dropdown-check-mark.svg');
        float: right;
      }
    }

    html[data-theme='clover'] & {
      &::after {
        color: var(--input-error);
        content: url('../../assets/icons/dropdown-check-mark-clover.svg');
        float: right;
      }
    }

    html[data-theme='bsc'] & {
      &::after {
        color: var(--input-error);
        content: url('../../assets/icons/dropdown-check-mark-bsc.svg');
        float: right;
      }
    }
  }

  .h3-select__option--is-focused {
    background-color: var(--interface-info-background);
    cursor: pointer;

    .arrow-key & {
      outline: 2px solid var(--focus-button-outline-color, #5995b3);
      outline-offset: -2px;
    }

    &:focus-visible {
      box-shadow: inset 0 0 0 2px var(--input-dropdown-focus);
    }

    &:hover {
      background-color: var(--interface-info-background);
      outline: none;
    }
  }

  ::-webkit-scrollbar {
    height: 10px;
    width: 14px;
  }

  ::-webkit-scrollbar-track-piece {
    -webkit-border-radius: 16px;
    border-radius: 16px;
  }

  ::-webkit-scrollbar-thumb:vertical {
    background-color: var(--scrollbar-background-color);
    border: 4px solid var(--scrollbar-bordercolor);
    -webkit-border-radius: 16px;
    border-radius: 16px;
    height: 10px;
  }

  ::-webkit-scrollbar-button:start:decrement,
  ::-webkit-scrollbar-button:end:increment {
    background-color: transparent;
    height: 10px;
  }
}
