@import '~assets/styles/h3Imports';

.label-element {
  display: inline-block;
  font-size: rem(14);
  line-height: line-height(20, 14);
  width: var(--input-label-width);
}

.text-element {
  background: var(--white, #ffffff);
  border-radius: var(--border-radius, 8px);
  box-shadow: 0 0 0 1px var(--input-default);
  color: var(--input-default);
  display: block;
  font-weight: 400;
  height: rem(48);
  max-width: var(--max-width-dropdown);
  min-width: var(--min-width-dropdown);
  width: 100%;

  &:hover .select-placeholder-text {
    color: var(--text-color);
  }

  &:hover {
    box-shadow: 0 0 0 2px var(--input-hover);

    svg {
      path {
        fill: var(--dropdown-icon);
      }
    }
  }

  svg {
    path {
      fill: var(--input-focus);
    }
  }

  &.disabled {
    background: var(--input-disabled-background);
    border: 1px solid var(--input-disabled);
    outline: 1px solid var(--input-disabled);
  }

  &.error {
    border-radius: var(--border-radius, 8px);
    outline: 1px solid var(--input-error);
  }

  &:valid {
    color: var(--text-color);
  }
}

.error-element {
  color: var(--input-error);
  font-size: rem(14);
  font-weight: 600;
  line-height: line-height(20, 14);
}

.required:not(.disabled)::before {
  color: var(--input-error);
  content: '*';
  margin-left: 4px;
}
